  text-align: left;
  color: #616070;
  width: 98px;
  height: 14px;
  margin-top: 4px;
  margin-left: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__reviews {
  justify-content: space-between;
  width: 166px;
  height: 16px;
  margin-top: 16px;
  margin-left: 16px;
  display: flex;
}

.product-card__reviews-title {
  text-align: right;
  color: #060b35;
  justify-content: flex-end;
  align-items: flex-end;
  width: 53px;
  height: 16px;
  font-size: 10px;
